.container-fluid {
  padding-right: 0!important;
  padding-left: 0!important;
}

.App {
  text-align: center;
  margin: 0px;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  background-color: var(--navy);
  color: var(--slate);
  font-family: var(--font-sans);
  font-size: var(--fz-xl);
  line-height: 1.3;
  display: table;
}

.App::after, .App::before {
  box-sizing: inherit;
}

.App-logo {
  height: 600px;
  background-image: url('./assests/images/MinionWelcome.gif');
  background-attachment: relative;
  background-position: center center;
  background-size: 100% 100%;
  margin-top: 45px;
  padding-top:100px;
  padding-left:50px;
  text-align: left;
  width: 100%;
  z-index: 1000;
}

#App-logo h1 {
	color:#ffffff;
	margin-top: 20px;
	font-size: 100px;
	font-weight: bold;
	text-shadow: 2px 2px 2px #000;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Email {
  width: 40px;
  position: fixed;
  bottom: 0px;
  left: auto;
  right: 40px;
  z-index: 10;
  color: var(--light-slate);
}

.Email-inner {
  display: flex;
  height: 500px;
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  position: relative;
}

.Email-inner a {
  margin: 5px auto;
  padding: 5px;
  font-family: var(--font-mono);
  font-size: var(--fz-xxs);
  letter-spacing: 0.1em;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}

.Email-inner a:hover, .Email-inner a:focus {
  transform: translateY(-3px);
  color:  #64ffda;
}

.Email-inner::after {
  content: "";
  display: block;
  width: 1px;
  height: 60px;
  margin: 0px auto;
  background-color: var(--red);
  }

.Email-inner::before {
    content: "";
    display: block;
    width: 1px;
    height: 90px;
    margin: 0px auto;
    background-color: var(--red);
} 

.Left-Vertical {
  width: 40px;
  position: fixed;
  bottom: 0px;
  left: 40px;
  right: auto;
  z-index: 10;
  color: var(--light-slate);
}

.Left-Vertical-Inner {
  display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    margin: 0px;
    padding: 0px;
    list-style: none;
}

.Left-Vertical-Inner::after {
  content: "";
  display: block;
  width: 1px;
  height: 200px;
  margin: 0px auto;
  background-color: var(--red);
}

.Left-Vertical-Inner::before {
  content: "";
  display: block;
  width: 1px;
  height: 150px;
  margin: 0px auto;
  background-color: var(--red);
}

.Left-Vertical-Inner li {
  padding: 12px;
}

.Left-Vertical-Inner li a i {
  width: 18px;
  height: 18px;
  fill: currentColor;
  vertical-align: middle;
}
.Left-Vertical-Inner a:hover, .Email-inner a:focus {
  transform: translateY(-3px);
  color:  #64ffda;
}

.about {
  max-width: 900px;
  margin: 0px auto;
  padding-top: 50px;
  padding-right: 0px;
  padding-left: 0px;
  padding-bottom: 0px;
  display: block;
}

.numbered-heading {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  position: relative;
  margin: 0.5px 0px 40px;
  white-space: nowrap;
}

.numbered-heading h1 h2, h3, h4, h5, h6 {
  margin: 0px 0px 10px;
  font-weight: 600;
  color: #64ffda;
  line-height: 1.1;
}

.numbered-heading::before {
  position: relative;
  bottom: 0px;
  margin-right: 10px;
  color: #64ffda;
  font-family: var(--font-mono);
  font-size: clamp(var(--fz-md),3vw,var(--fz-xl));
  font-weight: 600;
}

.numbered-heading::after {
  content: "";
  display: block;
  position: relative;
  top: 5px;
  width: 300px;
  height: 1px;
  margin-left: 20px;
  background-color:  #64ffda;
}

.about-inner {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 50px;
}

.about-image {
  position: relative;
  max-width: 500px;
}


.about-image .wrapper {
  border: 2px solid var(--green);
  box-shadow: 0 10px 30px -15px var(--navy-shadow);
  transition: var(--transition);
  display: block;
  position: relative;
  width: 100%;
  background-color: var(--green);
}

.about-image .wrapper::before {
  top: 0px;
  left: 0px;
  mix-blend-mode: screen;
}
.about-image .wrapper::after {
  border: 2px solid var(--green);
  top: 20px;
  left: 20px;
  z-index: -1;
}

.about-image .wrapper::before, .about-image .wrapper::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: var(--border-radius);
  transition: var(--transition);
}

.about-image .wrapper .imgDetails {
  border: 10px solid var(--red);
  border-radius: var(--border-radius);
  mix-blend-mode: multiply;
  filter: grayscale(100%) contrast(1);
  transition: var(--transition);
}

.about-image .wrapper .gatsby-image-wrapper {
  width: 100%;
  max-width: 100%;
  vertical-align: middle;
}

.about-image .wrapper:hover, .about-image .wrapper:focus {
  transform: translateY(-3px);
  background-color: var(--white);
}

.about-image .wrapper:hover::after, .about-image .wrapper:focus::after {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px -15px var(--red);
  background-color: var(--light-slate);
  z-index: -1;
  border: 2px solid var(--white);
}

.about-image .wrapper .imgDetails:hover, .about-image .wrapper .imgDetails:focus {
  transform: translateY(-3px);
  border: 2px solid var(--red);
  box-shadow: 0 10px 30px -15px var(--red);
  z-index: 1000;
}

.about-inner-text {
 text-align: justify;
}

.jobs {
  max-width: 900px;
  margin: 0px auto;
  padding: 50px 0px 0px 0px;
  display: block;
}

.jobs .inner {
  display: flex;
}
.jobDesc {
  text-align: left;
}

.skills {
  width: 900px;
  display: block;
  padding: 50px 0px 0px 0px;
}

.skills .row::before, .skills .row::after {
  display: table;
    content: " ";
}

.service {
  text-align: left;
}

.service small {
  font-size: 12px;
  font-weight: bold;
  color: #64ffda;
}

.service .text {
  color: #f7eac2;
}

.mail-wrapper {
  display: flex;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  flex-direction: column;
  align-items: flex-start;
  min-height: 100vh;
  margin: 0px auto;
  max-width: 1000px;
  padding-top:200px;
  padding-left: 300px;
}

.NavHeader {
  color: '#ccd6f6';
}

.NavHeader:hover, .NavHeader:focus {
  transform: translateY(-3px);
  color: #64ffda !important;
  text-decoration: none;
}

.home-search-bar {
  max-width: 350px;
  margin-right: auto;
  margin-bottom: 0px;
  min-height: 40px;
  border: 2px solid #64ffda;
  padding-top: 5px;
  padding-right: 9px;
  padding-bottom: 0px;
  padding-left: 9px;
  margin-top: 150px;
  margin-left: 0px;
}

.home-search-bar span {
  font: 20px arial,sans-serif;
  line-height: 26px !important;
  height: 26px !important;
  font-weight: 500;
}

.typed {
  animation: blink-animation 2.5s steps(5, start) infinite;
  -webkit-animation: blink-animation 2.5s steps(5, start) infinite;
}
.typed-cursor {
  opacity: 1;
  animation: blink-animation 1s steps(5, start) infinite;
  -webkit-animation: blink-animation 1s steps(5, start) infinite;
}

@keyframes blink-animation {
  to {
    visibility: hidden;
  }
}
@-webkit-keyframes blink-animation {
  to {
    visibility: hidden;
  }
}

.jobTabs:hover, .jobTabs:focus {
    transform: translateY(-3px);
}

.contact {
  max-width: 900px;
  margin: 0px auto;
  text-align: justify;
  padding: 50px 0px 0px 0px;
}

.contact .title {
  font-size: clamp(20px, 5vw, 40px);
}

.email-link {
  color: #64ffda;
  background-color: transparent;
  border: 1px solid #64ffda;
  border-radius: var(--border-radius);
  padding: 1.25rem 1.75rem;
  font-size: var(--fz-sm);
  font-family: var(--font-mono);
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: var(--transition);
  margin-top: 100px;
}
/* Header name animations */
.ml11 {
  font-weight: 500;
  font-size: 1.5em;
}

.ml11 .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.1em;
  padding-right: 0.05em;
  padding-bottom: 0.15em;
}

.ml11 .line {
  opacity: 0;
  position: absolute;
  left: 0;
  height: 100%;
  width: 3px;
  background-color: #fff;
  transform-origin: 0 50%;
}

.ml11 .line1 { 
  top: 0; 
  left: 0;
}

.ml11 .letter {
  display: inline-block;
  line-height: 1em;
}

/* footer */
.footerClass {
  background-color: #282c34;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  flex-direction: column;
  height: auto;
  min-height: 70px;
  padding: 15px;
  text-align: center;
}

.footerInner {
    color: var(--white);
    font-family: var(--font-mono);
    font-size: var(--fz-xxs);
    line-height: 1;
}

.footerInner  a {
  padding: 10px;
  display: inline-block;
  text-decoration: none;
  text-decoration-skip-ink: auto;
  color: inherit;
  position: relative;
  transition: var(--transition);
  cursor: pointer;
}

.footerContact {

  display: block;
  width: 100%;
  max-width: 270px;
  margin: 0px auto 2px;
  padding-left: 80px;
  padding-top: 10px;
  color: var(--light-slate)

}

.footerContactInner {

  display: flex;
  -webkit-box-pack: justify;
  -webkit-box-align: center;
  align-items: center;
  padding: 0px;
  margin: 0px;
  list-style: none;
}

.footerContactInner li {
  padding: 0px 12px 0px 0px;
}

.footerContactInner li a i {
  width: 18px;
  height: 15px;
}

.footerContactInner a:hover{
  transform: translateY(-3px);
  color:  #64ffda;
}

/* name animation */
.custom-container
{
  font-family: 'Open Sans', sans serif;
  font-size: 20px;
  font-weight: bold;
  margin-right:40px;
  margin-left: 100px;
  width: 170px;
  height: 30px;
  z-index:1020;
  border-color: #64ffda;
  border-radius: 10px;
  perspective: 500px;
}

.custom
{
  position: relative;
  height: 100%;
  border-radius: 10px;
  width: 100%;
  transform-style: preserve-3d;
}

.front,
.back
{
  width: 100%;
  height: 100%;
  border-radius: 10px;
  backface-visibility: hidden;
}

.front
{
  color: #64ffda;
  background: #282c34;
}

.back
{
  position: absolute;
  top: 0;
  left: 0;
  font-size: 18px;
  transform: rotateY(180deg);
  color: #ffff;
  background: #282c34;
}

.initial-container {
  font-family: 'Open Sans', sans serif;
  font-size: 30px;
  font-weight: bold;
  width: 50px;
  height: 40px;
  z-index:1020;
  border: 2px solid #64ffda;
  border-radius: 5px;
  perspective: 500px;
}

.initial {
  position: relative;
  height: 100%;
  border-radius: 10px;
  width: 100%;
  transform-style: preserve-3d;
}

.inner-text {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  backface-visibility: hidden;
  text-align: center;
  justify-content: center;
  color: #f7eac2;
  background: #282c34;
}

.counter {
  font: 800 40px system-ui;
}

